import Vue from 'vue'
import App from './App.vue'
import router from './router'
import CompositionApi from '@vue/composition-api'
import Qaxd from '@atsfe/qaxd'
import './styles/index.scss'

Vue.config.productionTip = false
Vue.use(CompositionApi)
Vue.use(Qaxd)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
